.UserAccount{
	height: 7vh;
	background: #fdfdfe;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-align-items: center;
	-ms-flex-align: center;
	    align-items: center;
	padding: 0 1.2rem;
}

.UserAccount p,.UserAccount p i{
	font-size: 0.8rem;
	color: rgba(25,25,25,.6);
}

.UserAccount p i{
	margin-left: 0.2rem
}

.changePassword{
	width: 100%;
	height: 93vh;
	background: #fdfdfe;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center
}
.changePassword p:first-child{
	font-size: 1rem;
	margin: 1rem 0;
}
.changePassword p input{
	width: 90vw;
	text-align: left;
	border: none;
	height: 3rem;
	border-bottom: 1px solid rgba(0,0,0,.2);
	text-indent: 2rem;
	outline: none;
	font-size: 0.8rem;
	background:  #fdfdfe
}

.changePassword button{
	width: 90%;
	margin-top: 2rem;
	height: 2rem;
	line-height: 2rem;
	background: #1acb9c;
	border: none;
	color: rgba(0,0,0,.3);
}



